import { createWebHistory, createRouter } from "vue-router";
import JH_HOME from './components/JH_HOME.vue'
import JH_LIST from './components/JH_LIST.vue'
import JH_LIST2 from './components/JH_LIST2.vue'
import JH_LIST3 from './components/JH_LIST3.vue'
import JH_LIST4 from './components/JH_LIST4.vue'

const routes = [
  {
    path: "/hoolog/",
    component: JH_HOME,
  },
  {
    path : "/hoolog/jhlist",
    component : JH_LIST,
  },
  {
    path : "/hoolog/jhlist2",
    component : JH_LIST2,
  },
  {
    path : "/hoolog/jhlist3",
    component : JH_LIST3,
  },
  {
    path : "/hoolog/jhlist4",
    component : JH_LIST4,
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

/* const router = createRouter({
  history: createWebHistory(),
  routes: [{ path: '/:pathMatch(.*)', component: NotFoundComponent }],
}); */

export default router; 